<template>
	<div class="create_template">
		<el-header height="20px">
			<h4 class="template_title">{{name}}</h4>
		</el-header>
		<el-container>
			  <el-main class="padding-left">
			  		<el-steps direction="vertical" :active="5" space='auto'>
						   <el-step title="" ref="_obj" class="info-step">
							   	<div slot="description" class="stepContent">
							   		<div class=" stepIcon">
										<div class="stepIcon-title">1</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
							   		<el-form ref="monitor_obj" :model="monitor_obj" label-width="110px" label-position="left" size="small">
										<el-form-item label="" prop="server" required>
										    <el-select v-model="server" placeholder="" class="width_430">
										      <el-option label="1" value="shanghai"></el-option>
										      <el-option label="2" value="beijing"></el-option>
										    </el-select>
										</el-form-item>
										<el-form-item label="" prop="server" required>
										    <el-select v-model="server" placeholder="" class="width_430">
										      <el-option label="1" value="shanghai"></el-option>
										      <el-option label="2" value="beijing"></el-option>
										    </el-select>
										</el-form-item>
										<el-form-item label="" class="label_width" required>
											<el-switch v-model="value2" active-color="#13ce66"
												  inactive-color="#d7deec"></el-switch>
										</el-form-item>
										<el-form-item label="" prop="server" required v-if="value2">
										    <el-select v-model="server" placeholder="" class="width_430">
										      <el-option label="1" value="shanghai"></el-option>
										      <el-option label="2" value="beijing"></el-option>
										    </el-select>
										</el-form-item>
							   		</el-form>
							   	</div>
						   </el-step>
						   <el-step title="" ref="_index" class="network-step">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">2</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
						   			<el-form ref="monitor_index" :model="monitor_index" label-width="110px" label-position="left">
						   				<div class="">
												<el-table
											      :data="tableData"
											      style="width: 100%">
											      <el-table-column label="">
											      	<template slot-scope="scope">
												        <el-select v-model="server" placeholder="" size="small">
													      <el-option label="1" value="shanghai"></el-option>
													      <el-option label="2" value="beijing"></el-option>
													    </el-select>
												    </template>
											      </el-table-column>
											      <el-table-column label="">
											      	<template slot-scope="scope">
												        <el-select v-model="server" placeholder="" size="small">
													      <el-option label="1" value="shanghai"></el-option>
													      <el-option label="2" value="beijing"></el-option>
													    </el-select>
												    </template>
											      </el-table-column>
											      <el-table-column label="">
											      	<template slot-scope="scope">
												        <el-select v-model="server" placeholder="" size="small">
													      <el-option label="1" value="shanghai"></el-option>
													      <el-option label="2" value="beijing"></el-option>
													    </el-select>
												    </template>
											      </el-table-column>
											      <el-table-column label="">
											      	<template slot-scope="scope">
												        <el-input-number v-model="num8" controls-position="right" :min="1" :max="100" size="small" class="width_100"></el-input-number>
												    </template>
											      </el-table-column>
											      <el-table-column label="">
											      	<template slot-scope="scope">
												        <el-select v-model="server" placeholder="" size="small">
													      <el-option label="1" value="shanghai"></el-option>
													      <el-option label="2" value="beijing"></el-option>
													    </el-select>
												    </template>
											      </el-table-column>
											      <el-table-column label="">
											      	<template slot-scope="scope">
												        <el-input-number v-model="num8" controls-position="right" :min="1" :max="100" size="small" class="width_100"></el-input-number>
												    </template>
											      </el-table-column>
											      <el-table-column label="">
											      	<template slot-scope="scope">
												        <el-button size="mini" type="text"
												          @click="delRule(scope.$index, scope.row)"></el-button>
												    </template>
											      </el-table-column>
											    </el-table>
											</div>
											<el-button type="text" @click.stop="addRule" icon="el-icon-circle-plus"></el-button>
											<label class="f_c_6">18</label>
						   			</el-form>
						   		</div>
						   </el-step>
						   <el-step title="" ref="_rule" class="create-step">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">3</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
						   			<el-form ref="monitor_rule" :model="monitor_rule" label-width="110px" label-position="right" size="small">
						   				<el-form-item label="" prop="" required>
										    <el-input placeholder="" v-model="obj_list_sr" clearable  class="width_430"></el-input>
										</el-form-item>
										<el-form-item label="" prop="">
										    <el-input
											  type="textarea"
											  :rows="4"
											  placeholder=""
											  v-model="textarea"
											  class="width_430">
											</el-input>
										</el-form-item>
						   			</el-form>
						   		</div>
						   </el-step>
						</el-steps>
			  </el-main>
		</el-container>
		<el-footer height="40px">
			<el-button type="info" @click.stop="cancel"></el-button>
			<el-button type="primary" @click.stop="order('indirect')"></el-button>
		</el-footer>
	</div>
</template>

<script>
export default {
	components:{
		
	},
	data () {
		return {
			name: '',
			server: 'shanghai',
			value2: true,
			num8: 20,
			obj_list_sr: '',
			textarea: '',
			tableData: [
				{

				},
				{

				}
			],
			monitor_obj: {

			},
			monitor_index: {

			},
			monitor_rule: {
			
			}

		}
	},
	methods: {
		addRule(){
			this.tableData.push({});
		},
		delRule(index,row){
		this.tableData.splice(index, 1);
		}
    },
    computed: {
    	
    },
    mounted: function () {
    	
    },
    beforeDestroy: function () {

    }
}
</script>

<style lang="scss">
.create_template{
	.el-step__main {
		background-color: #fff;
	    margin-left: 10px;
	    margin-bottom: 20px;
	    border-radius: 3px;
	    padding-top: 10px;
	}
	.padding-left{
		padding-left:0;
	}
	.el-button--text{
		color: #3dc7fa;
	}
	.el-button--primary{
		background-color: #3dc7fa;
	    border-color: #3dc7fa;
	}
	.el-button--primary.is-disabled{
		background-color: #a0cfff;
	    border-color: #a0cfff;
	}
	.el-step__head.is-finish{
		color: #3dc7fa;
	    border-color: #3dc7fa;
	}
	.network-step > .el-step__head.is-finish,.login-step > .el-step__head.is-finish,.create-step > .el-step__head.is-finish,.config-step > .el-step__head.is-finish {
		padding-top: 10px;
	}
	.info-step > .el-step__head.is-finish {
		margin-top: 10px;
	}
	.el-step:last-of-type .el-step__line{
		display : block;
		bottom: 20px;
		margin-right: 0px;
	}
	.el-step:last-of-type .el-step__line > i{
		transition-delay: 450ms;
	    border-width: 1px;
	    height: 100%;
	}
	.el-step__title {
		color: #3a3232 !important;
		padding-left: 30px;
	}
	.stepContent{
		padding-left: 18px;
		padding-right: 20px;
	    padding-bottom: 20px;
	    margin-bottom: 20px;
	}
	.stepIcon{
		position: absolute;
	    z-index: 1;
	    left: 0;
	    top: 9px;
	    border-radius: 50%;
	    border: 2px solid #3dc7fa;
	    align-items: center;
	    width: 24px;
	    height: 24px;
	    font-size: 14px;
	    background-color: #3dc7fa;
	    line-height: 2px;
	}
	.stepIcon-round{
		border-radius: 50%;
	    border: 2px solid #3dc7fa;
	    align-items: center;
	    width: 24px;
	    height: 24px;
	    font-size: 14px;
	    background-color: #3dc7fa;
	    line-height: 2px;
	    position: relative;
	}
	.stepIcon-title{
		position: absolute;
	    top: 10px;
	    left: 7px;
	    color: #fff;
	    text-align: center;
	}
	.stepIcon-rectangle{
		background-color: #3dc7fa;
	    width: 30px;
	    height: 20px;
	    margin-left: 20px;
	    position: relative;
	    top: 2px;
	}
	.stepIcon-triangle{
		width: 0;
	    height: 0;
	    border-top: 10px solid transparent;
	    border-left: 15px solid #3dc7fa;
	    border-bottom: 10px solid transparent;
	    position: absolute;
	    right: -15px;
	}
	.el-step__description {
		padding-right: initial;
	}
	.template_title {
	 	font-size: 14px;
    	color: #333;
    	padding-left: 12px;
	}
	.label_width>label {
		width: 180px !important;
	}
	.width_430 {
		width:430px;
	}
	.width_100 {
		width:100px;
	}
	.el-table th{
	  	padding: 3px 0;
	  	background-color: #f0f2f7;
	 }
	 .el-table td{
	  	padding: 10px 0;
	 }
	 .f_c_6{
	 	color: #666;
	 }
}
</style>


