import request from '@/utils/request'
import { getRegionId} from '@/utils/auth'

/************************/
/**/
export function getMonitorOptions(params) {
	params.regionid || (params.regionid = getRegionId());
	return request({
		url:'console/ces/html/select/options/',
		method:'get',
		params:params
	})
}
/************************/
/**/
