import axios from 'axios'
import { Message } from 'element-ui'
import store from '@/store'
import { getToken } from '@/utils/auth'
import exception from '@/utils/exception'



let baseUrl = process.env.NODE_ENV === "production" ? '/' : '/api/';
// axios
let cancelToken = axios.CancelToken;
const service = axios.create({
  baseURL: baseUrl, // apibase_url
  timeout: 50000, // 
})

service.cancle = null;
// request
service.interceptors.request.use(config => {
     config.cancelToken = new cancelToken((c)=>{
     /*	console.log(config.url);*/
     	service.cancle = c;//servicecancle
				
    });
  // Do something before request is sent
  if (store.getters.token) {

		if(config.method == 'get'){
			let newDate = (new Date()).getTime()
			config.params ? (config.params.timeStamp = newDate ) : (config.params  = {
				timeStamp:newDate,
			})
		}
  	config.headers['X-CSRFToken'] = getToken() // token--['X-Token']key 
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => response ,
  /**
  * responsecodecode
  * xmlhttprequest  error
  */
  //  const res = response.data;
  //     if (res.code !== 20000) {
  //       Message({
  //         message: res.message,
  //         type: 'error',
  //         duration: 5 * 1000
  //       });
  //       // 50008:token; 50012:;  50014:Token ;
  //       if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
  //         MessageBox.confirm('', '', {
  //           confirmButtonText: '',
  //           cancelButtonText: '',
  //           type: 'warning'
  //         }).then(() => {
  //           store.dispatch('FedLogOut').then(() => {
  //             location.reload();// vue-router bug
  //           });
  //         })
  //       }
  //       return Promise.reject('error');
  //     } else {
  //       return response.data;
  //     }
  error => {
    console.log('err' + error)// for debug
    if (error.response) {
    	let errorMsg = exception(error) || error.message;
      //  2xx 
      /*console.log(error.response.data);
      console.log(error.response.status);
      console.log(error.response.headers);*/
      if(error.response.status == 401){
      	console.log(error.response.data);
      	console.log(error.response.status);
      	console.log(error.response.headers);

      	store.dispatch('FedLogOut').then(() => {
           /* Message.error('')*/
            location.href='#/login'
        })
      }else{
	      Message({
		      message: errorMsg,
		      type: 'error',
		      duration: 5 * 1000,
		      showClose:true
		    })
      }


    } else {
      // Something happened in setting up the request that triggered an Error
      console.log('Error', error.message);
      if(error.message == 'CancelToken'){
      	
      }else{
      	Message({
		      message: "",
		      type: 'error',
		      duration: 5 * 1000,
		      showClose:true
		    })
      }
    }
    /*console.log(error.config);*/

    return Promise.reject(error)
  })

export default service



