<template>
	<div class="main">
		<el-row class="user-title-row">
			<el-col>
				<div class="user-title">
					
				</div>
			</el-col>
		</el-row>
		<el-row class="border-bottom">
			<el-col :span="22" class="user-type-title">
					<span  class="user-title"> </span>
					<span class="user-type-vip">
						<svg-icon icon-class="vip" style="margin-right:5px;font-size:14px"></svg-icon>
						{{userItems.userInfo ? 'VIP' : 'VIP'}}
					</span>
			</el-col>
			<el-col :span="2" class="editInfo" v-if="editModule">
				<svg-icon icon-class="edit"></svg-icon>
				<el-button type="text" @click='editInfo'>
					
				</el-button>
			</el-col>
		</el-row>
		<el-row class="user-type">
			<el-col :span="4">
				<div class="user-img">
					<img :src="userBig"/>
				</div>
				<div class="user-type-editImg" v-if="editModule">
					<el-button type='text' @click="editImg">
						
					</el-button>
				</div>
			</el-col>
			<el-col :span="20" class="user-type-items">
				<el-col :span="item.span || 12" v-for = '(item,index) in userItems.userType' :key="item.label" class="user-type-item">
					{{item.label}} : {{item.value}}
				</el-col>
			</el-col>
		</el-row>
		<el-row class="border-bottom" v-if="userItems.userInfo">
			<el-col :span="22" class="user-title">
				 
			</el-col>
			<el-col :span="2" class="editInfo" v-if="editModule">
				<svg-icon icon-class="edit"></svg-icon>
				<el-button type="text" @click="editInfo">
					
				</el-button>
			</el-col>
		</el-row>
		<el-row class="user-info" v-if="userItems.userInfo">
			<el-col :span='12' v-for='item in userItems.userInfo' :key='item.label' class="user-info-item">
				{{item.label}} : {{ item.value}}
			</el-col>
		</el-row>
		<el-row class="border-bottom">
			<el-col  class="user-title">
				
			</el-col>
		</el-row>
		<el-row class="pwdRow" >
			<el-col class="pwdInfo">
				<div>
					
				</div>
				<div class="pwdProgress">
					<el-progress :percentage="password_strength ? 100 : 20 " :status="password_strength ? 'success' : 'exception'"></el-progress>
				</div>
				<div>
					<el-button type="text" class="editButton" @click="editPwd">
						
					</el-button>
				</div>
			</el-col>
			<el-col class="pwdTip">
				8-20
			</el-col>
		</el-row>
		<el-row  class="border-bottom" v-if="editModule">
			<el-col :span="22" class="user-title">
				 
			</el-col>
			<el-col :span="2" class="editInfo">
				<svg-icon icon-class="edit"></svg-icon>
				<el-button type="text">
					
				</el-button>
			</el-col>
		</el-row>
		<el-row class="pwdRow" v-if="editModule">
			<el-col >
				
				<span v-for="item in tipList" class="tipSetting">
					<svg-icon icon-class="correctInCircle" :class="item.status ? 'correct' : 'error'" v-if="item.status"></svg-icon>
					<svg-icon icon-class="error" :class="item.status ? 'correct' : 'error'" v-if="!item.status">></svg-icon>
					{{item.value}}
				</span>
			</el-col>
			<el-col class="pwdTip">
				
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import userBig from '@/assets/user-big.png';
	export default {
		data(){
			return {
				msg:'',
				user:{},
				userBig,
				password_strength:'',
				editModule:false,
				userType:'personal',
				userInfo:{
					company:{
						userType:[
							{
								label:'',
								id:'account',
								value:'--',
								span:24
							},
							{
								label:'',
								id:'company_name',
								value:'--'
							},
							{
								label:'',
								id:'company_type',
								value:'--'
							},
							{
								label:'ID',
								id:'account_id',
								value:'--'
							},
							{
								label:'',
								id:'create_date',
								value:'--'
							}
						],
						userInfo:[
							{
								label:'',
								id:'customer_name',
								value:'--',
							},
							{
								label:'',
								id:'sex',
								value:'--',
							},
							{
								label:'',
								id:'email',
								value:'--',
							},
							{
								label:'',
								id:'mobile',
								value:'--',
							},
							{
								label:'',
								id:'address',
								value:'--',
							}
						]
					},
					personal:{
						userType:[
							{
								label:'',
								id:'name',
								value:'--',
							},
							{
								label:'',
								id:'customer_name',
								value:'--',
							},
							{
								label:'',
								id:'company_name',
								value:'--'
							},
							{
								label:'',
								id:'email',
								value:'--'
							},
							{
								label:'',
								id:'phone',
								value:'--'
							},
							{
								label:'',
								id:'address',
								value:'--'
							}
						]
					}
				},
				tipList:[
					{
						status:true,
						value:''
					},
					{
						status:true,
						value:''
					},
					{
						status:false,
						value:''
					}
				]
				
				
			}
		},
		methods:{
			
		},
		computed:{
			userItems(){
				let userTypeList = this.userInfo[this.userType].userType;
				userTypeList.map(item=>{
					item.value = this.user[item.id] || '--'
				})
				
				return this.userInfo[this.userType];
			}
		},
		created(){
			this.user = this.$store.state.user.user;
			this.password_strength = this.user.password_strength;
			console.log(this.user)
		},
		methods:{
			editImg(){
				console.log('')
			},
			editPwd(){
				console.log('')
				this.$router.push('/user/userInfo/editPwd')
			},
			editInfo(){
				console.log('')
			}
		}
		
		
	}
</script>

<style lang="scss" scoped="scoped">
	.main{
		background: #fff;
		height: 100%;
		font-size:12px;
		color:#333333;
		padding:0px 20px;
	}
	.correct{
		color:#56D6C4
	}
	.error{
		color:#fa767c;
	}
	.border-bottom{
		font-size:14px;
		line-height: 34px;;
		border-bottom:1px dashed #dce0e3;
	}
	.user-title-row{
		padding-top:10px;
		line-height:34px;
	}
	.user-title{
		font-size:14px;
		font-weight: 600;
	}
	.user-type-title{
		
	}
	.user-type-vip{
		font-size:12px;
		color:#F9CD76;
		margin-left:15px;
	}
	.user-type{
		padding:20px;
		.user-img{
			padding:0 20px;
			width:75%;
			box-sizing:border-box;
			img{
				width:100%;
			}
		}
		.user-type-editImg{
			width:75%;
			text-align: center;
			.el-button--text{
				color:#3dc7fa;
				font-size:12px;
			}
		}
		.user-type-items{
			padding-top:20px;
			.user-type-item{
				line-height: 32px;;
			}
		}
	}
	.user-info-title{
		
	}
	.user-info{
		padding-top:10px;
		.user-info-item{
			line-height: 32px;
			font-size:12px;
		}
		
	}
	.editInfo{
		font-size:14px;
		color:#3dc7fa;
		.el-button--text{
			color:#3dc7fa;
			font-size:12px;
		}
	}
	
	.editButton.el-button--text{
		color:#3dc7fa;
		font-size:12px;
	}
	.pwdRow{
		line-height: 32px;
		font-size:12px;
		padding:20px 0;
		.pwdInfo{
			>div{
				display: inline-block;
			}
			.pwdProgress{
				width:400px;
				padding:0 10px 0 20px;
			}
		}
		.pwdTip{
			color:#666666;
		}
	}
	.tipSetting{
		padding-right:10px;
	}
	
</style>


