<template>
	<div class="billInfo">
		<el-container>
			<el-header height="100px">
				<h4 class="bill_info_title"></h4>
				<div class="bill_info_tip">20171110110</div>
				<el-date-picker
					v-model="date"
					type="date"
					placeholder=""
					default-value="2010-10-01" size="small">
				</el-date-picker>
				<el-button type="primary" size="small"></el-button>
				<el-button type="primary" size="small" class="right"></el-button>
			</el-header>
			<el-main>
				<el-table :data="tableData" headerCellClassName="tableHeaderbg" :emptyText="emptyText">
				        <el-table-column prop="name" label="" align="center"></el-table-column>
				        <el-table-column prop="name" label="" align="center"></el-table-column>
				        <el-table-column prop="name" label="" align="center"></el-table-column>
				        <el-table-column prop="address" label="" align="center"></el-table-column>
				        <el-table-column prop="address" label="" align="center"></el-table-column>
				</el-table>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		
		data (){
			return {
				date:'',
				emptyText: '',
				tableData: [],
			}
		}
	}
</script>

<style>
.billInfo {
	background-color: #fff;
	height: 100%;
}
.tableHeaderbg {
	background-color: #f0f2f6 !important;
	color: #333;
}
.bill_info_title {
	margin: 15px 0;
}
.bill_info_tip {
	color: #f9cd76;
	font-size: 12px;
	margin-bottom: 15px;
}
.right {
	float: right;
}
</style>


