
var state = {
	running:'',
	stopped:'',
	active:'',
	shutoff:'',
	
	present:'',
	destroyed:'',
	expunging:'',
	expunged:'',
	rebuild:'',
	resize:'',
	stopping:'',
	starting:'',
	restarting:'',
	ready:'',
	subnetactive:'',
	error:'',
	mounting:'',
	mount:'',
	'in-use':'',
	'available':'',
	uninstall:'',
	uninstalling:'',
	allocated:'',
	allocatin:'',
	releasing:'',
	unbinding:'',
	down:'',
	up:'',
	binding:'',
	bindings:'',
	unbundings:'',
	instance:'',
	portforwarding:'',
	loadbalancer:'',
	backedup:'',
  	implemented:'',
	backingup:'',
	allocateds:'',
	implemented:'',
	setup:'',
	implementing:'',
	
	creating:'',
	inactive:'',
	enabled:'',
}


export function statFormat(item){
	item = item && item.toLowerCase();
	return state[item];

}

let volumeType = {
	'SATA':'IO',
	'SAS':'IO',
	'SSD':'IO'
};
export function diskType(item){
	return volumeType[item];
}


